.header-color {
    background: #1f4037;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #99f2c8, #1f4037); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    
}

.landing-grid {
    background: #1f4037;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #99f2c8, #1f4037); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    


position: fixed;
top: 0;
left: 0;
width: 100%;
height: 100%;
text-align: center;

}

.contact-body{
    margin: auto;
    position: fixed;
    width: 100%;
    height: 100%;
    -webkit-justify-content: center;
            justify-content: center;
    padding-top: 2em;
}

.contact-grid{
    text-align: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: 80%;
    height: 550px;
    background: white;
    color: black;
    margin-bottom: 20px;
}

.contact-grid h2{
    font-family: 'Anton', sans-serif;
}
.contact-grid p{
    font-family: 'Oxygen', sans-serif;
    font-weight: bold;
}
.contact-grid hr{
    border-top: 5px dotted black;
    width: 50%;
    margin: auto;
}
.contact-list i{
    font-size: 66px;
    padding-right: 5rem;
}
.avatar-img {
    height : 250px;
    padding-top: 5em;
    border-radius: 100px;
}

.banner-text {
    background-color: black;
    opacity: .8;
    width: 75%;
    margin: auto;
    border-radius: 10px;
}

.projects-grid{
    display: -webkit-flex;
    display: flex;
}

.banner-text h1{
    font-size: 66px;
    font-weight: bold;
    color: white;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.banner-text hr {
    border-top: 5px dotted white;
    width: 50%;
    margin: auto;
}

.banner-text p {
    color: white;
    font-size: 20px;
    padding: 1em;
}
.social-links{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 50%;
    margin: auto;
}
.social-links i {
    font: white;
    font-size: 5em;
    padding: 8px;
}
.resume-right-col{
    background: #27221F;
    color: white;
    padding: 2em;

}
